
import XButton from 'vux/src/components/x-button/index.vue'

import Header from "@/components/common/Header";
import CellNoIcon from "@/components/common/CellNoIcon";
import { GetAuthState } from "@/servers/api";

export default {
name: "",
props: [""],
data() {
return {
TitleObjData: {
titleContent: "设置",
showLeftBack: true,
showRightMore: false
},
authState: -1,
cellListToolsA: [
{
title: "实名认证",
link: "/realnameauth",
badge: true,
text: "未认证"
},






{
title: "昵称",
link: "",
badge: false,
text: ""
}
],
cellListToolsE: [
{
title: "实名认证",
link: "/realnameauthstate",
badge: false,
text: ""
},






{
title: "昵称",
link: "",
badge: false,
text: ""
}
],
cellListToolsB: [
{
title: "二维码",
link: "/tokenqrcode",
badge: false,
text: ""
}
],
cellListToolsC: [
{
title: "收货地址",
link: "/address",
badge: false,
text: ""
}
],
cellListToolsD: [
{
title: "关于我们",
link: "/aboutus",
badge: false,
text: ""
},
{
title: "意见反馈",
link: "/opinion",
badge: false,
text: ""
}
]
};
},

components: {
Header,
CellNoIcon,
XButton
},

computed: {
conHei() {
return { height: document.documentElement.clientHeight - 45 + "px" };
}
},

beforeMount() {},

mounted() {
this.getAuthState();
},

methods: {
quitAppLogin() {
localStorage.removeItem("token");
this.$router.push("/");
},

getAuthState() {
GetAuthState({})
.then(res => {
if(res){
this.authState = res.data.code;
}
})
.catch(err => {
console.log(err);
});
}
},

watch: {}
};



